@use "@/scss/utils/function" as *;
@use "@/scss/utils/mixins" as *;
.diagramConteiner {
  width: 100%;
  background-color: white;
  @include laptop {
    height: calc(100vh - #{rem(910)});
  }
  @include tablet {
    height: calc(100vh - #{rem(810)});
  }
  @include mobile {
    height: calc(100vh - #{rem(670)});
  }
}

