@use "@/scss/utils/function" as *;
@use "@/scss/utils/mixins" as *;
// animationContainer ----------------------------------------------------------

.animationContainer {
    width: 100vw;
    background-color: #f0f0f0;
}
// Bug ----------------------------------------------------------
.bug {
	width: rem(30);
	height: rem(110);
	padding: rem(30);
	margin: auto calc(12.5% - 30px) 0 auto;
	box-sizing: border-box;
	//background: greenyellow;
	border-radius: 50px;
}
// Yellow Conteiner ----------------------------------------------------------

.yellowCircleConteiner {
	width: 100%;
    display: flex;
	align-items: center;
	overflow-x: auto;
	scroll-snap-type: x mandatory;
	scrollbar-width: none;
	padding: rem(9) 0;
	border-top: rem(1) solid grey;
    border-bottom: rem(1) solid grey;
	box-sizing: border-box;
	transition: transform 0.3s ease; /* Плавне переміщення */
}
// Green Conteiner ----------------------------------------------------------

.greenCircleConteiner {
	position: relative;
	display: flex;
	align-content: start;
	flex-wrap: wrap;
	padding: rem(10) 0 ;
	row-gap: rem(10);
	overflow-y: auto;
	scroll-snap-type: y mandatory;
	scrollbar-width: none;
	@include laptop {
		height: 590px;
	}
	@include tablet {
		height: 470px;
	}
	@include mobile {
		height: 350px;
	}
}
// Complete Button -----------------------------------------------

.completeButton {
	width: 100%;
	height: rem(60);
	background-color: orange;
	position: absolute;
	left: 0;
	bottom: -120px; ///
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 20px;
	color: white;
	box-sizing: border-box;
	z-index: 1;
	transition: 0.3s bottom ease-in-out;
}
.changeAnimation {
	bottom: -60px;
}
// Change animation ----------------------------------------

.rotate {
	animation: rotateBackAndForth 0.4s ease-in-out infinite;
}

@keyframes rotateBackAndForth {
	0% {
		transform: rotate(-2deg);
	}
	50% {
		transform: rotate(2deg);
	}
	100% {
		transform: rotate(-2deg);
	}
}



